/* 노란색 배경 (새로 추가된 행) */
.highlight-yellow {
    background-color: #ffffe7 !important;
}

/* 푸른색 깜빡임 (수정된 행) */
@keyframes blinkBlue {
    0% {
        background-color: transparent;
    }
    50% {
        background-color: #98a8ff;
    }
    100% {
        background-color: transparent;
    }
}

.blink-blue {
    animation: blinkBlue 0.5s ease-in-out 2;
}
