.abp-header {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}

.abp-close-btn {
    background: none;
    border: none;
    font-size: 24px;
    font-weight: bold;
    color: #333;
    cursor: pointer;
    margin: 0;
    padding: 0;
}

.abp-close-btn:hover {
    color: #f00;
}

.abp-detail-container {
    border: 1px solid #ddd;
    padding: 20px;
    background-color: #fff;
    border-radius: 6px;
}

.abp-detail-container h3 {
    margin-top: 0;
    margin-bottom: 16px;
    font-weight: 600;
    color: #3f51b5;
}

.abp-detail-row {
    display: flex;
    align-items: center;
    margin-bottom: 6px;
}

.abp-detail-row label {
    width: 120px;
    font-weight: 600;
    margin-right: 8px;
}

/* 공통 스타일 (두 모드 모두 동일) */
.abp-detail-row input {
    flex: 1;
    font-size: 14px;
    padding: 6px 8px;
    line-height: 1.5;
    height: 36px;            /* 고정 높이(선택사항) */
    box-sizing: border-box;
    border-radius: 4px;
    transition: border-color 0.2s;
}

/* 편집 모드 */
.abp-detail-row input:not(.read-only) {
    border: 1px solid #ccc;     /* 실제 테두리 표시 */
    background-color: #fff;
}

/* 읽기 전용 모드 */
.abp-detail-row input.read-only {
    border: 1px solid transparent; /* 테두리 공간은 같지만 색만 투명 */
    background-color: transparent;
    cursor: default;
}

/* 포커스(편집 모드 시) */
.abp-detail-row input:focus {
    outline: none;
    border-color: #3f51b5;
}

/* 버튼들 */
button {
    padding: 8px 14px;
    font-size: 14px;
    font-weight: 600;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    margin-right: 8px;
    /*transition: background-color 0.2s ease;*/
}

.abp-edit-btn {
    margin-left: auto;
    background-color: #3f51b5;
}
.abp-edit-btn:hover {
    background-color: #303f9f;
}

.abp-cancel-btn {
    margin-left: auto;
    background-color: #d63031;
}
.abp-cancel-btn:hover {
    background-color: #c0392b;
}

.abp-save-btn {
    background-color: #00b894;
}
.abp-save-btn:hover {
    background-color: #019875;
}

.abp-cancel-booking-btn {
    background-color: #aaaaaa;
}
.abp-cancel-booking-btn:hover {
    background-color: #9a9a9a;
}
