.abp-table-responsive {
    width: 100%;
    overflow-x: auto;  /* 가로 스크롤 */
}

.abp-table {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 20px;
    font-size: 14px;

    /* 컬럼 너비를 고정하기 위해 추가 */
    table-layout: fixed;
    /* page max-width와 동일하게 설정 */
    min-width: 960px;
}

.abp-table colgroup col {
    /* 위에서 인라인 style로 width를 지정했지만,
       여기서도 min-width를 설정해줄 수 있음 */
    /* min-width: 100px; */
}

.abp-table thead tr {
    background-color: #3f51b5;
    color: #fff;
}

.abp-table th,
.abp-table td {
    border: 1px solid #ddd;
    padding: 8px 10px;
    text-align: left;
    vertical-align: middle;
    word-wrap: break-word; /* 내용이 길면 줄바꿈 */
}

.abp-table tbody tr:nth-child(even) {
    background-color: #fafafa;
}

.abp-table tbody tr:hover {
    background-color: #dae0ff;
    cursor: pointer;
}

.abp-table tbody tr.highlight {
    background-color: #fff5d3;
}

/* 모바일 폰트 크기 조절 */
@media screen and (max-width: 768px) {
    .abp-table {
        font-size: 12px;
    }
}

.flash {
    animation: flashAnimation 1s ease-in-out;
}

@keyframes flashAnimation {
    0% {
        background-color: #9289ff;
    }
    50% {
        background-color: #fff;
    }
    100% {
        background-color: #9289ff;
    }
}
